import React from 'react'
import styled from 'styled-components'
import{selectPopular} from "../features/movies/movieSlice";
import {useSelector} from "react-redux"
function Movies() {

    const popular = useSelector(selectPopular);
console.log(popular);
  return (
    <Container>
       <h4>Recommended For You</h4> 
        <Content>

            {
                
               popular && Object.entries(popular).map((movie) => (
                <Wrap >
                
                        {/* <p>{movie.Description}</p> */}
                    <img src= {movie.TitleImg} />
                    
               
                 </Wrap>
                // <Wrap>
                // <img src="https://lumiere-a.akamaihd.net/v1/images/p_luca_21670_3c13c611.jpeg" alt="" />
                //  </Wrap>

            ))}
            <Wrap>
                <img src="https://lumiere-a.akamaihd.net/v1/images/p_luca_21670_3c13c611.jpeg" alt="" />
                 </Wrap>
                 <Wrap>
                <img src="https://lumiere-a.akamaihd.net/v1/images/p_luca_21670_3c13c611.jpeg" alt="" />
                 </Wrap>
                 <Wrap>
                <img src="https://lumiere-a.akamaihd.net/v1/images/p_luca_21670_3c13c611.jpeg" alt="" />
                 </Wrap>
                 <Wrap>
                <img src="https://lumiere-a.akamaihd.net/v1/images/p_luca_21670_3c13c611.jpeg" alt="" />
                 </Wrap>
                 <Wrap>
                <img src="https://lumiere-a.akamaihd.net/v1/images/p_luca_21670_3c13c611.jpeg" alt="" />
                 </Wrap>
                 <Wrap>
                <img src="https://lumiere-a.akamaihd.net/v1/images/p_luca_21670_3c13c611.jpeg" alt="" />
                 </Wrap>
                 <Wrap>
                <img src="https://lumiere-a.akamaihd.net/v1/images/p_luca_21670_3c13c611.jpeg" alt="" />
                 </Wrap>
                 <Wrap>
                <img src="https://lumiere-a.akamaihd.net/v1/images/p_luca_21670_3c13c611.jpeg" alt="" />
                 </Wrap>
                 



        </Content>
        </Container>
  )
}

export default Movies

const Container = styled.div`

`
const Content = styled.div`
display:grid;
grid-gap:25px;
grid-template-columns:repeat(4,minmax(0,1fr));
`
const Wrap = styled.div`
border-radius:10px;
cursor:pointer;
overflow:hidden;
border: 3px solid rgba(249, 249,249,0.1);
box-shadow: rgb(0 0 0 / 69%) 0px 26px 38px -10px,
rgb(0 0 0 / 73%) 0px 16px 10px -10px;
transition:all 250ms cubic-bezier(0.25,0.46,0.45,0.94) 0s;

img{
    width:100%;
    height:100%;
    object-fit:cover;
}

&:hover{
    box-shadow: rgb(0 0 0 / 69%) 0px 26px 38px -10px,
rgb(0 0 0 / 73%) 0px 16px 10px -10px;
    transform:scale(1.05);
    border-color: rgba(249, 249, 249, 0.8);
}
`